.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  gap: 20px;
  transition: opasity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 90%;
  height: 90%;
  background-color: brown;
  border-radius: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  overflow: auto;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal__btn {
  width: 30px;
  height: 30px;
